body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  width: 550px;
  height: 95vh;
  margin: 10px auto;
  border-radius: 15px;
  overflow-y: scroll;
}

.app::-webkit-scrollbar {
  width: 1.1vh;
}

.app::-webkit-scrollbar-track {
  background: #80808022;
  margin-block-start: 45px;
  border-radius: 25px;
}
.app::-webkit-scrollbar-thumb {
  background: #d60000dd;
  border-radius: 25px;
  }

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  background: #d60000dd;
  color: #fff;
  border-radius: 15px 15px 0 0;
  font-weight: 700;
}

.header img {
  width: 22px;
  margin-right: 5px;
}

.card {
  display: flex;
  justify-content: space-around;
  margin: 20px;
}

.card>img {
  width: 25%;
  margin-right: 20px;
  object-fit: cover;
}

.imgUrl {
  flex-shrink: 0;
}

.card h3{
  margin-bottom: 15px;
}

.info img {
  width: 10px;
}

.location, .mapUrl {
  font-size: 0.8em;
}

.mapUrl {
  color: #808080;
  margin-left: 20px;
}

.date {
  font-weight: 600;
  font-size: 0.9em;
}
